.works {
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 90vh;
    margin-top: 4rem;

}


.awesome {
    display: flex;
    flex-direction: column;
    flex: 1;
    position: relative;
    /* width: 40%; */
}

.awesome span:nth-of-type(3) {
    width: 80%;
}

/* right side */
.w-right {
    position: relative;
    flex: 1;
}

.w-mainCircle {
    left: 5rem;
    position: relative;
    width: 18rem;
    height: 18rem;
    border-radius: 100%;
    box-shadow: var(--smboxShadow);
    top: 2rem;
    background: white;
}

.w-mainCircle>* {
    position: absolute;
}

.w-mainCircle> :nth-child(1) {
    top: -3rem;
    left: 6rem;
}

.w-mainCircle> :nth-child(2) {
    left: -3rem;
    top: 5rem;
}

.w-mainCircle> :nth-child(3) {
    left: 6rem;
    top: 5rem;
}

.w-mainCircle> :nth-child(4) {
    left: 15rem;
    top: 5rem;
}

.w-mainCircle> :nth-child(5) {
    left: 6rem;
    top: 13rem;
}

.w-secCircle {
    width: 6rem;
    height: 6rem;
    position: absolute;
    border-radius: 100%;
    border: 5px solid #ebebeb;
    display: flex;
    align-items: center;
    justify-content: center;
    background: "none";
    box-shadow: var(--smboxShadow);
    background: white;
}

.w-secCircle>img {
    transform: scale(0.6);
}

.w-backCircle {
    position: absolute;
    height: 13rem;
    width: 13rem;
    border-radius: 100%;
    z-index: -1;
}

.blueCircle {
    background: #1949b8;
    left: 9rem;
    top: -1rem;
}

.yellowCircle {
    background: #f5c32c;
    left: 9rem;
    top: 6rem;
}


@media screen and (max-width: 480px) {
     .works {
        flex-direction: column;
        height: 48rem;
        padding: 0;
        gap: 4rem;
        text-align: center;
     }
     .w-right {
        display: flex;
        align-items:flex-start;
        justify-content: center;
        transform: scale(0.7);
     }
     .w-mainCircle {
        position: static;
     }
}









