.intro {
    display: flex;
    height: 77vh;
    margin-top: 6rem;
}

.i-left {
    display: flex;
    position: relative;
    flex: 1;
    flex-direction: column;
    gap: 2rem;

}

.i-name {
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
}

.i-name>:nth-child(1) {
    color: var(--black);
    font-weight: bold;
    font-size: 3rem;
}

.i-name>:nth-child(2) {
    color: var(--black);
    font-weight: bold;
    font-size: 2rem;
}

.i-name span {
    color: var(--orange);
    font-weight: bold;
    font-size: 2rem;
}
 

.i-name>:nth-child(3) {
    font-weight: 100;
    font-size: 16px;
    color: var(--grey);
    margin-top: 10px;
    width: 70%;
}

.i-button {
    width: 7rem;
    height: 2rem;
}

.i-icons {
    margin-top: 3rem;
    display: flex;
    /* gap: 0rem; */
}

a>img {
    transform: scale(0.5);
}

.i-icons>*:hover {
    cursor: pointer;
}


.i-right {
    flex: 1;
    position: relative;
}

.i-right>* {
    position: absolute;
    z-index: 1;
}

.i-right>:nth-child(1) {
    transform: scale(0.69);
    left: -15%;
    top: -9%;
}

.i-right>:nth-child(2) {
    transform: scale(0.67);
    top: -4.6rem;
    left: -3rem;
}

.i-right>:nth-child(3) {
    transform: scale(1.4);
    left: 28%;
}

.i-right>:nth-child(4) {
    transform: scale(0.6);
    top: -19%;
    left: -24%;
    border-radius: 50%;
    padding: 0;
}

#profile-pic {
    width: 285px;
    height: 386px;
}

@media screen and (max-width: 480px) {
  .intro {
    flex-direction: column;
    height: 58rem;
    gap: 4rem;
    margin-top: 2rem;
  }
  .i-left {
    align-items: center;
    text-align: center;
     
  }
  .i-name>:nth-child(2) {
    height: 5rem;
}

  .i-name>:nth-child(3) {
       width: 100%;
}

  .i-icons {
    margin-top: 1rem;
  }
  .i-right {
    transform: scale(0.8);
    left: -3rem;
  }
  .blur {
    display: none;
  }
}















